@-webkit-keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes marker {
  to {
    background-color: #f6f6f6;
  }
}

@keyframes marker {
  to {
    background-color: #f6f6f6;
  }
}

.root {
  position: relative;
}

.tip {
  display: none;
  left: 50%;
  position: absolute;
  top: 0;
  -webkit-transform: translate(-50%, -100%) translateY(-12px);
          transform: translate(-50%, -100%) translateY(-12px);
  white-space: nowrap;
}

.root:hover > .tip {
  display: inline-block;
  -webkit-animation: fade .2s linear;
          animation: fade .2s linear;
}

.root:hover > .marker {
  background-color: #f6f6f6;
  -webkit-animation: marker .2s linear forwards;
          animation: marker .2s linear forwards;
}



