.button {
  border-radius: 2px;
  border-width: 0;
  display: inline-block;
  font-weight: 700;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1;
  padding: 12.8px;
  padding: .8rem;
  text-decoration: none;
  -webkit-transition: opacity .1s ease-out;
  transition: opacity .1s ease-out;
}

.button:hover {
  opacity: .7;
}

.default {
  background-color: inherit;
  border: 1px solid #ddd;
  color: #8c8c8c;
}

.primary {
  background-color: #51c300;
  color: #fff;
}

.warning {
  background-color: #f0163a;
  color: #fff;
}



